	background-color: #5995f7;
	color: white;
}

.routing {
	border-radius: 50%;
}

.routing.message{
	border-radius: 50% 50% 0 0;
}

.bpm-header{
	color: darkblue;
	margin: 20px 10px 10px 20px;
}

.bp-objects-container {
	position: relative;
	width: 100%;
	max-width: 200px;
}

